/* eslint-disable */
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

//className 선언 없이 css스타일 사용하기
let 박스 = styled.div`
    padding : 20px;
`;
let 제목 = styled.h4`
    font-size : 20px;
    color : ${ props => props.색상 }
`;

//Lifecycle Hook 과거 버전
// class Detail2 extends React.Component {
//     componentDidMount(){
//     }
//     componentWillUnmount(){
//     }
// }

function Detail(props) {
    
    let [alert, alert변경] = useState(true);
    let [inputData, inputData변경] =useState('');
    let { id } = useParams();
    let history = useHistory(); //history 방문기록이 담긴 object
    let 찾은상품 = props.shoes.find(상품 => 상품.id == id);
    // let 찾은상품 = props.shoes.find(function(상품){
    //     return 상품.id == id
    // })
    

    //useEffect Hook => component가 mount/update 되었을 때 특정 코드를 실행할 수 있음
    useEffect(()=>{
        //3초후에 alert창 닫음.
        let 타이머 = setTimeout(()=>{alert변경(false)}, 3000);
        return ()=>{ clearTimeout(타이머) };
    },[]); 
    //,[] => 실행되는 조건 넣을 수 있다. 아무것도 없으면 페이지가 처음 로드될 때만 실행.
    //,[alert] => alert라는 state가 update될 때 만 실행시킨다.
    //,[alert, inputData] => alert과 inputData state가 update될 때 실행시킨다.


    return (
        <div className="container">
            <박스>
                {/* <제목 색상="red" >Detail</제목> */}
                <제목 className="red">Detail</제목>
            </박스>


            <input onChange={(e)=>{ inputData변경(e.target.value) }} />

            {
                alert === true
                ? (<div className="my-alert2">
                     <p>재고가 얼마 남지 않았습니다</p>
                   </div>)
                : null
            }
            
            <div className="row">
                <div className="col-md-6">
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{찾은상품.title}</h4>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price}원</p>
                    <Info 재고={props.재고}></Info>

                    <button className="btn btn-danger" onClick={()=>{
                        props.재고변경([9,11,12]);
                    }}>주문하기</button>
                    &nbsp; {/* 공백주기 */}
                    <button className="btn btn-danger" onClick={()=>{ 
                        history.push('/');
                        //history.goBack(); //뒤로가기   
                    }}>뒤로가기</button> 
                    {/* 특정경로로 이동 history.push('/경로'); */}
                </div>
            </div>
        </div> 
    )  
}

//재고 component
function Info(props) {
    return (
        <p>재고 : {props.재고[0]}</p>
    )
}


export default Detail;